@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Arimo);
@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#root {
  width: 100%;
  font-family: "Poppins", "sans serif";
}
body {
  background-color: #ffffda;
}

#calculator {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border: 1px black solid;
  padding: 10px;
  background-color: #444444;
}

#display {
  height: 80px;
  padding: 10px;
  text-align: right;
  font-size: 1.6em;
  margin-bottom: -30px;
  color: white;
}

#calculation {
  height: 30px;
  padding: 10px;
  margin-bottom: -20px;
  text-align: right;
  color: #ffff66;
}

.square {
  width: 25%;
  height: 50px;
  font-size: 1.3em;
  padding: 3px;
}

#clear,
#zero {
  width: 50%;
}

#buttons {
  margin: 10px;
}
#clear {
  background-color: #ff7f7f;
}

#equals {
  height: 100px;
  float: right;
  background-color: #e0ffff;
}

@font-face {
  font-family: digital;
  src: url(/frontend-compilation/static/media/digital7.93911ab1.ttf);
}

#body {
  background-color: rgb(53, 53, 53);
}

#drum-machine {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border: 4px solid rgb(255, 194, 103);
  padding: 20px;
  display: flex;
  justify-content: space-around;
  width: 40%;
  height: 300px;
  background-color: rgb(198, 210, 220);
}

#board {
  flex: 55% 1;
  margin-left: -20px;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.drum-pad {
  width: 25%;
  margin: 2px;
  border-radius: 10px;
  background-color: rgb(36, 36, 36);
  color: wheat;
  font-size: 1.3em;
}

#display {
  background-color: lavender;
  flex: 1 1;
  text-align: right;
  width: 90%;
  height: 50px;
  padding: 10px;
  font-size: 1.9em;
  font-family: digital;
  margin-top: 30px;
  margin-bottom: 50px;
}

#controls {
  flex: 45% 1;
  justify-content: space-around;
  margin-left: 10px;
}

#switch {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin-left: calc(50% - 30px);
  margin-bottom: 20px;
  align-self: flex-start;
  text-align: center;
}

.MuiSlider-root {
  color: antiquewhite;
}

.MuiTypography-body1 {
  color: black;
}

body {
  background-color: #dcedc1;
}

#container {
  margin: 20px auto;
  padding: 25px;
  height: 640px;
}

#editor {
  display: block;
  background-color: #f1f2ef;
  margin-left: auto;
  margin-right: auto;
  width: 35%;
  height: 600px;
  padding: 15px;
  font-size: 1.2em;
  float: left;
}

#preview {
  width: 55%;
  font-size: 1.2em;
  font-family: 'Arimo';
  border: 2px solid black;
  background-color: #ffffff;
  padding: 15px;
  float: right;
  height: 600px;
  overflow: auto;
}

#preview img {
  max-width: 90%;
}
body {
  background-color: #7ad5d5;
}

#root {
  font-family: "Merienda";
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

#Clock {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
}

#title {
  font-size: 3em;
  width: 100%;
  text-align: center;
  margin: 30px;
}

#timer {
  width: 80%;
  text-align: center;
  font-size: 1.5em;
  margin: 10px;
  border: 2px black solid;
  border-radius: 25px;
  padding: 5px;
}

button {
  margin: 15px;
  align-self: center;
  font-size: 1.5em;
}

#break-label,
#session-label {
  width: 50%;
  text-align: center;
  font-size: 1.2em;
}
body {
  height: 100%;
  margin: 0;
}

#page {
  position: fixed;
  padding: 0;
  margin: 0;

  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  transition: background-color 800ms linear;
}

#quote-box {
  background-color: white;
  padding: 30px;
  width: 35%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  font-family: "Raleway", "Proxima Nova", "Oxygen", "sans serif";
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
blockquote {
  font-size: 1.75em;
  text-align: center;
  font-weight: bold;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

blockquote:before {
  position: absolute;
  content: open-quote;
  font-size: 4em;
  margin-left: -0.5em;
  margin-top: -0.4em;
}

#author {
  text-align: right;
  margin: 20px;
  display: block;
  margin-top: 5px;
}

#new-quote {
  float: right;
  border: none;
  color: white;
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  font-size: 1em;
}
.fa {
  padding: 5px;
  font-size: 1em;
  width: 6%;
  text-align: center;
  text-decoration: none;
}

.fa:hover {
  opacity: 0.7;
}

/* Twitter */
.fa-twitter {
  background: #55acee;
  color: white;
}

